* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
}

body {
  width: 100%;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;

  background-image: url(./absctract\ bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;

  font-family: "Raleway", sans-serif;
  color: #fff;
}

button {
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
}
